import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index'
import invest from '@/pages/invest'
import error from '@/pages/error'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            component: index
        },
        {
            path: '/invest',
            component: resolve => require(['@/pages/invest'], resolve)
        },
        {
            path: '/about',
            component: resolve => require(['@/pages/about'], resolve)
        },
        {
            path: '/dinner',
            component: resolve => require(['@/pages/dinner'], resolve)
        },
        {
            path: '/test',
            component: resolve => require(['@/pages/test_fake'], resolve)
        },
        {
            path: '/vkdsijhu4hnmdjki',
            component: resolve => require(['@/pages/test'], resolve)
        },
        {
            path: '/*',
            component: error
        }
    ]
})

export default router
